<template>
    <div id="textBoxContainer" :style="'height:' + height + ';width:' + width">

        <div id="tHeader" v-if="header != null">
            {{header}}
        </div>

        <div id="tBody" :style="flexStyle">
            <slot name="body"></slot>
        </div>

        <div id="tFooter"  v-if="enableArrow">
            <img src="../assets/arrow-right.png" id="arrowBtn" @click="arrowFunc">
        </div>
        
    </div>
</template>

<script>
export default{
    props:{
        height: String,
        width: String,
        enableArrow: Boolean,
        header: String,
        arrowFunc: Function,
    },
    computed:{
        flexStyle(){
            return this.header!=null ? '':'justify-content: center'
        }
    }
}
</script>

<style scoped>

#textBoxContainer{
    border: solid 2px black;
    box-shadow: calc(5px + 0.8vw) calc(5px + 0.8vw);
    overflow-x: hidden;
    overflow-y: auto;

    display: flex;
    flex-direction: column;
    justify-content: space-evenly;

    font-size: calc(1.6em + 0.15vw);

    max-width: 80vw;
    max-height: 80vw;
}

#tHeader{
    height: 8%;
    text-align: left;
    margin: 2% 2% 0 2%;
    font-weight: bold;
}

#tBody{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: left;
    margin: 0 2% 0 2%;
    overflow: auto;
}

#tFooter{
    height: 12%;
    margin: 0 5% 0 2%;
}

#arrowBtn{
    float: right;
    width: 40%;
    cursor: pointer;
}

@media only screen and (max-width: 800px) {
  #textBoxContainer{
    font-size: calc(0.5em + 2.5vw);
    margin-left: 0;
  }

}
</style>