<template>
    <div id="page">
        <div id="textBoxContainer">

            <span v-html="convertPostText(tposts[currentPostId]['postText'])"></span>

        </div>
    </div>
</template>

<script>
const tposts = require('../assets/dummyPosts.json');

export default {
  name: 'App',
  components: {
  },
  mounted(){
    this.fetchNewPost();
    const onClick = (event) => {
      var elementID = event.srcElement.id
      if(elementID[0] == "#"){
        this.fetchNewPost();
        this.currentHashTag = elementID.split("#")[1]
      }
    }
    window.addEventListener('click', onClick);
  },
  data(){
    return{
      tposts,
      currentHashTag: "",
      currentPostId: '0',
      maxAttemptsToFindNewpost: 25,
    }
  },
  methods:{
    // Converts post to remove the # and turn following words into links
    convertPostText(text){ 
      return text.replace(/#(\w+)/g, `<a class="hashTag" id="#$1">$1</a>`)
    },
    fetchNewPost(){
      var tpostsLen = Object.keys(tposts).length;
      var randomPost = Math.floor(Math.random() * tpostsLen);

      if(this.currentHashTag != ""){
        var counter = 0;
        while(!tposts[randomPost]["hashTags"].includes(this.currentHashTag) && this.currentPostId == randomPost){
          randomPost = Math.floor(Math.random() * tpostsLen);
          if(counter > this.maxAttemptsToFindNewpost) { 
            console.log("Failed to find new post")
            break;
          }
        }
      }
      this.currentPostId = randomPost;
    }
  },
}
</script>

<style scoped>

#page{
  width: 100vw;
  height: 100vh;
  background: rgb(255, 255, 255);
  color: rgb(0, 0, 0);

  display: flex;
  justify-content: center;
  align-items: center;
}

#textBoxContainer{
  border: solid 2px rgb(0, 0, 0);
  box-shadow: calc(5px + 0.8vw) calc(5px + 0.8vw) rgb(0, 0, 0);
  overflow-x: hidden;
  overflow-y: auto;
  padding: 10px;
  font-size: calc(1.6em + 0.15vw);
  height: 500px;
  width: 500px;
  max-width: 80vw;
  max-height: 80vw;
}



@media only screen and (max-width: 800px) {
  #textBoxContainer{
    font-size: calc(0.5em + 2.5vw);
  }
}
</style>

<style>

.hashTag{
  color: red;
  text-decoration: underline;
  cursor: pointer;
}

</style>