<template>

    <span class="material-icons" id="navToggleBtn" @click="toggleNavPanel()" :style="'color:' + navButtonColor"> menu </span>
     
    <transition name="slide">
        <div id="navBarContainer" v-if="navPanelEnabled">
            <div id="navLinkContainer">
                <div class="navLinkText">Log In</div>
                <div class="navLinkText" @click="pushToRouter('readStory')">Read a Story</div>
                <div class="navLinkText" @click="pushToRouter('writeStory')" >Write a Story</div>
                <div class="navLinkText" @click="pushToRouter('storyhelp')">Story Help</div>
                <div class="navLinkText">FAQ</div>
            </div>
            <div class="navLinkText" @click="pushToRouter('/')">About Linked</div>
        </div>
    </transition>
</template>

<script>
export default{
    data(){
        return{
            navPanelEnabled: false,
        }
    },
    methods:{
        toggleNavPanel(){
            this.navPanelEnabled = !this.navPanelEnabled;
        },
        pushToRouter(destination){
            this.navPanelEnabled = false;
            this.$router.push(destination)
            
        }
    },
    computed:{
        navButtonColor(){
            return this.navPanelEnabled ? 'white':'black';
        }
    }
}
</script>

<style scoped>

#navBarContainer{
    user-select: none;
    z-index: 9;
    position: absolute;
    right: 0px;
    top: 0;
    background: black;
    height: 100vh;
    width: 420px;

    transition: transform 0.8s ;

    color: white;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    font-size: calc(1.8em + 0.5vw);
    text-align: left;
}

#navToggleBtn{
    z-index: 10;
    user-select: none;
    position: absolute;
    right: 0;
    top: 0;
    margin: 5px 8px 0 0;
    font-size: 4em;
    color: white;
    cursor: pointer;
    transition: color .8s;
}

#navLinkContainer{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    margin-top: 1.5em;
}

.navLinkText{
    color: white;
    margin-left: 20px;
    margin-bottom: 2%;
    cursor: pointer;
}

/* Screen queries */
@media only screen and (max-width: 550px) {
  #navBarContainer {
    width: 80vw;
  }

  #navToggleBtn{
    font-size: calc(2em + 2vw);
  }
}

/* Transitions */
.slide-enter-from, .slide-leave-to /* .fade-leave-active below version 2.1.8 */ {
  transform: translateX(450px);
}
</style>
