import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'
import writeStory from '../views/writeStory.vue'
import storyIdeaHelp from '../views/storyIdeaHelp.vue'
import readStory from '../views/readStory.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/writeStory',
    name: 'writeStory',
    component: writeStory
  },
  {
    path: '/storyhelp',
    name: 'storyhelp',
    component: storyIdeaHelp
  },
  {
    path: '/readStory',
    name: 'readStory',
    component: readStory,
  }

]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
